.informacion{
    @include telefono{
        width: 90%;
        margin: 0 auto;
        padding-top: 2rem;
    }
    @include tablet{
        width: 70%;
        margin: 0 auto;
        padding: 1rem;
    }

}
.img-cuenta{
    border: 2px inset $verde;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    margin: 0 auto;
    margin-bottom: 1rem;
    label{
        position: absolute;
        bottom: -15px;
        right: -10px;
        text-align: center;
        padding: 12px;
        width: 3rem;
        height: 3rem;
        background-color: $verde;
        color: $blanco;
        border-radius: 50%;
        cursor: pointer;
        font-size: 1.5rem;
        box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
    }
}
.control-file{
    display: none;
}
.grupo-campo{
    @include telefono{
        display: block;
    }
    @include tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
        margin: 1rem 0;
    }
    .campo{
        width: 100%;
        label{
            display: block;
            color: darken($verde, 15);
            padding: 1rem 0;
        }
        input{
            width: 100%;
        }
    }
}
.titulo h3{
    font-size: 1.3rem;
    color: darken($azul, 15);
    font-weight: 400;
    margin-bottom: 1rem;
}